.uplink {
    width: 50px;
    height: 50px;
    border-radius: 5px;
    border: none;
    background-color: #f2662f;
    position: relative;
    bottom: 50px;
    right: 100px;

    &::after {
        content: "";
        width: 13px;
        height: 25px;
        background: url('./../images/footer/go-top-arrow.png') no-repeat center center;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        -webkit-transform: translate(-50%,-50%);
        -moz-transform: translate(-50%,-50%);
        -ms-transform: translate(-50%,-50%);
        -o-transform: translate(-50%,-50%);
    }
}






































    






















